const randomMonsterName = () => {
  const monsterName = [['Ужасный', 'Злобный', 'Сопливый'], ['Огр', 'Гном', 'Гоблин'], ['Том', 'Макс', 'Дима']];
  const currentMonsterName = [];
  for (let i = 0; i < 3; i += 1) {
    currentMonsterName.push(monsterName[i][Math.floor(monsterName[i].length * Math.random())]);
  }
  return currentMonsterName.join(' ');
};
export default randomMonsterName;
